import getTime from "../../../getTime.js";
export default {
	data() {
		return {
			TabCur: 0,
			tabRow: ['当前隐患','历史隐患'],
			startTime: '',
			endTime: '',
			now: '',
			curList: [],
			hisList: [],
			nodata: 'http://data.huwei-soft.cn/schooltest/static/nodata.png',
			search2: 'http://data.huwei-soft.cn/schooltest/static/search2.png'
		}
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		},
		changetime(e) {
			if (e.target.dataset.time == 'start') {
				this.startTime = e.target.value;
			} else {
				this.endTime = e.target.value;
			}
		},
		getCurList() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Cur_Problems',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime
				}
			}).then(data => {
				var [error, res] = data;
				// console.log(res.data)
				if (res.data == '暂无数据') {
					self.curList = []
				} else {
					let foo = JSON.parse(res.data) 
					self.curList = foo;
				}
			})
		},
		getHisList() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/His_Problems',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime,
					Keyword: ''
				}
			}).then(data => {
				var [error, res] = data;
				if (res.data == '暂无数据') {
					self.hisList = []
				} else {
					let foo = JSON.parse(res.data)
					self.hisList = foo;
				}
			})
		},
		SearchMore() {
			uni.showLoading({
				title:"加载中",
				mask: true
			})
			this.getCurList();
			this.getHisList();
			this.$nextTick(() => {
				uni.hideLoading()
			})
		},
		
		showCurDetail(item) {
			uni.navigateTo({
				url: 'searchDangerDetail/searchDangerDetail?YHXH='+item.YHXH + '&No=' + item.todoid + '&state=0'
			})
		},
		showHisDetail(item) {
			uni.navigateTo({
				url: 'searchDangerDetail/searchDangerDetail?YHXH='+item.YHXH + '&No=' + item.todoid + '&state=1'
			})
		}
	},
	mounted() {
		this.now = getTime.now();
		uni.showLoading({
			title:"加载中",
			mask: true
		})
		this.getCurList();
		this.getHisList();
		this.$nextTick(() => {
			uni.hideLoading()
		})
	}
}
